import React, {useCallback, useRef, useEffect, useState} from 'react';
import Typist from 'react-typist';
import isEmail from 'is-email';
import useOnKeyDown from '../Hooks/useOnKeyDown';
import Moment from 'react-moment';

const cursor = {blink : true, element : "‚ñà", hideWhenDone: true, hideWhenDoneDelay : 0};

export const Contact = React.memo(({onDone}) => {
	
	const input = useRef(null);
	const body = useRef(null);
	
	const smtpGen = useCallback((mean, std, {line, lineIdx, charIdx, defDelayGenerator}) => {
		if(body.current !== null)
			body.current.scrollTop = body.current.scrollHeight;
		
		return 3.33;
	}, [body]);
	
	const [now, setNow] = useState(Date.now() / 1000);
	const [currentValue, setCurrentValue] = useState('');
	const [contactName, setContactName] = useState('');
	const [contactMail, setContactMail] = useState('');
	const [contactSubject, setContactSubject] = useState('');
	const [contactMessage, setContactMessage] = useState('');
	const [retryEmail, setRetryMail] = useState(false);
	const [stage, setStage] = useState(0);
	const [sending, setSending] = useState(false);
	
	useEffect(() => {
		
		if(body.current !== null)
			body.current.scrollTop = body.current.scrollHeight;
		
		if(stage !== 4 || sending || contactName.length === 0 || contactMail.length === 0 || contactMessage.length === 0 || contactSubject.length === 0)
			return;
		
		setNow(Date.now() / 1000);
		setSending(true);
		
		const mailBody = {
			email : contactMail,
			name : contactName, 
			body : contactMessage,
			subject : contactSubject
		};
		
		fetch(new URL('http://gabrielmartz.net/api/contact.php'), {
			method : 'POST',
			headers: {
				'Content-Type' : 'application/json;charset=utf-8'
			},
			body : JSON.stringify(mailBody)
		})
		.then(() => {}, onDone);
		
	}, [body, stage, sending, onDone, contactName, contactMail, contactMessage, contactSubject]);
	
	const onEnter = useCallback(() => {
		if(currentValue.length === 0)
			return;
		
		let success = false;
		
		switch(stage){
			case 0:
				setStage(1);
				setContactName(currentValue);
				setCurrentValue('');
				success = true;
				break;
			case 1:
				if(isEmail(currentValue)){
					setStage(2);
					setContactMail(currentValue);
					setCurrentValue('');
				success = true;	
				}else{
					setRetryMail(true);
					setCurrentValue('');
				}
				break;
			case 2:
				setStage(3);
				setContactSubject(currentValue);
				setCurrentValue('');
				success = true;
				break;
			case 3:
				setStage(4);
				setContactMessage(currentValue);
				setCurrentValue('');
				success = true;
				break;
			default:
				break;
		}
		
		if(success && body.current !== null)
			body.current.scrollTop = body.current.scrollHeight;
		
	}, [currentValue, stage]);
	
	const onMobileSubmit = useCallback(e => {
		onEnter();
		e.preventDefault();
	}, [onEnter]);
	
	const onKeyDown = useCallback((e) => {
		
		console.log(e.code);
		
		if(e.code === 'Escape'){
			onDone();
			return;
		}
		
		if(e.code === 'Enter')
			onEnter();
	}, [onDone, onEnter]);
	
	useOnKeyDown(onKeyDown);
	
	useEffect(() => {
		if(input.current == null)
			return;
		
		let inp = input.current;
		inp.focus();
	}, [input]);
	
	const catchText = useCallback(e => {
		setCurrentValue(e.target.value);
	}, []);
	
	const focusMe = useCallback(e => {
		input.current.focus();
	}, [input]);
	
	return  <>
		<p>Fill the following fields to contact <span className="userChoice">Gabriel Martz</span>. Press <u onClick={onDone}>[ESC]</u> to go back.</p>
		<div className="contentBody border-up" ref={body}>
			{ stage === 4 && contactMessage.length > 0 ? 
				<>
					<Typist cursor={cursor} delayGenerator={smtpGen} onTypingDone={() => setTimeout(onDone, 100)}>
						SERVER -&gt; CLIENT: 220-mail.gabrielmartz.net ESMTP Exim 4.92 #2 <Moment unix={true} format="ddd, DD MMM YYYY HH:mm:ss ZZ"date={now}/> 220<br/><Typist.Delay ms={100}/>
						CLIENT -&gt; SERVER: EHLO gabrielmartz.net<br/><Typist.Delay ms={100}/>
						SERVER -&gt; CLIENT: 250-mail.gabrielmartz.net Hello gabrielmartz.net [127.0.0.1]250-SIZE 52428800250-8BITMIME250-PIPELINING250-AUTH PLAIN LOGIN250-CHUNKING250-STARTTLS250-SMTPUTF8250 HELP<br/><Typist.Delay ms={100}/>
						CLIENT -&gt; SERVER: MAIL FROM:&lt;contact@gabrielmartz.net&gt;<br/><Typist.Delay ms={100}/>
						SERVER -&gt; CLIENT: 250 OK<br/><Typist.Delay ms={100}/>
						CLIENT -&gt; SERVER: RCPT TO:&lt;me@gabrielmartz.net&gt;<br/><Typist.Delay ms={100}/>
						SERVER -&gt; CLIENT: 250 Accepted<br/><Typist.Delay ms={100}/>
						CLIENT -&gt; SERVER: DATA<br/><Typist.Delay ms={100}/>
						SERVER -&gt; CLIENT: 354 Enter message, ending with "." on a line by itself<br/><Typist.Delay ms={100}/>
						CLIENT -&gt; SERVER: Date: <Moment unix format="ddd, DD MMM YYYY HH:mm:ss ZZ"date={now}/><br/><Typist.Delay ms={100}/>
						CLIENT -&gt; SERVER: To: Gabriel Martz &lt;me@gabrielmartz.net&gt;<br/><Typist.Delay ms={100}/>
						CLIENT -&gt; SERVER: From: Contact &lt;contact@gabrielmartz.net&gt;<br/><Typist.Delay ms={100}/>
						CLIENT -&gt; SERVER: Reply-To: "{ contactName }" &lt;{ contactMail }&gt;<br/><Typist.Delay ms={100}/>
						CLIENT -&gt; SERVER: Subject: { contactSubject }<br/><Typist.Delay ms={100}/>
						CLIENT -&gt; SERVER: Message-ID: &lt;09HDEihGV1SpqPIky7PZUMyM3qzrfuSfM0dzAS0@gabrielmartz.net&gt;<br/><Typist.Delay ms={100}/>
						CLIENT -&gt; SERVER: MIME-Version: 1.0<br/><Typist.Delay ms={100}/>
						CLIENT -&gt; SERVER: Content-Type: text/plain; charset=UTF-8<br/><Typist.Delay ms={100}/>
						CLIENT -&gt; SERVER: <br/><Typist.Delay ms={100}/>
						CLIENT -&gt; SERVER: { contactMessage }<br/><Typist.Delay ms={100}/>
						CLIENT -&gt; SERVER: <br/><Typist.Delay ms={100}/>
						CLIENT -&gt; SERVER: .<br/><Typist.Delay ms={100}/>
						SERVER -&gt; CLIENT: 250 OK id=1iYbgO-00GILC-6I<br/><Typist.Delay ms={100}/>
						CLIENT -&gt; SERVER: QUIT<br/><Typist.Delay ms={100}/>
						SERVER -&gt; CLIENT: 221 mail.gabrielmartz.net closing connection<br/><Typist.Delay ms={1000}/>
					</Typist>
				</> :
				<>
				<form onSubmit={onMobileSubmit}  className="hiddenInput">
					<input type="text" value={currentValue} className="hiddenInput" onChange={catchText} ref={input} onBlur={focusMe}/>
				</form>
				<b>Input your name?</b><br/> { stage === 0 ? <>{currentValue}<span className="cursorBlink">‚ñà</span></> : contactName}<br/>
				{ stage > 0 && 
						<>
							<b> {retryEmail ? 'Please input a valid electronic mailing address' : 'Input your electronic mailing address?'}</b>
							<br/>
							{ stage === 1 ? <>{currentValue}<span className="cursorBlink">‚ñà</span></> : contactMail}<br/>
						</>
				}
				{ stage > 1 && 
						<>
							<b>Input the subject of your message</b>
							<br/>
							{ stage === 2 ? <>{currentValue}<span className="cursorBlink">‚ñà</span></> : contactSubject}<br/>
						</>
				}
				{ stage > 2 && 
						<>
							<b>Input your message, press [ENTER] and your message will be sent</b>
							<br/>
							{ stage === 3 ? <>{currentValue}<span className="cursorBlink">‚ñà</span></> : contactMessage}<br/>
						</>
				}</>
			}
		</div>
	</>;
});